.button {
    width: 120px;
    height: 35px;
    padding: 4px;
    background-color: #3e68f1;
    border: none;
    cursor: pointer;
    transition: all 0.5ms ease-in-out;
    margin: 10px;
}
.button:hover {
    background-color: transparent;
    color: black;
    border: 1px solid #3e68f1;

}


.disabled {
    background-color: #616363;

}

.secondary {
    background-color: transparent;
    border: 1px solid #3e68f1;
    color: #3e68f1;

}
.secondary:hover {
    background-color: #3e68f1;
    color: white;
}
.default {
  color: white;
}

.red {
    background-color: #e95345;

}

.default:hover {
}

.red:hover {
    background-color: #e22a19;

}



.default:active {
}

.secondary:active {
}

.red:active {
}

.button:disabled {
    background: #fff6f6;
}
