}

.nav {
    display: flex;
    flex-direction: column;
}

.nav a {
    font-size: 28px;
    margin-bottom: 20px;
    color: black;
    text-decoration: none;
}

.nav a:hover {
    color: #3ac1de;
}

.nav a.active {
    color: #3ac1de;
    text-decoration: underline;

}
.close {
    position: absolute;
    top: 5px;
    right: 5px;
    border: transparent;
    background-color: transparent;
